import React from 'react'

function Price({ salePrice, originalPrice }) {
    return (
        <div>
            {salePrice ? (
                <>
                    <span className="book__price--normal">
                        {originalPrice.toFixed(2)}
                    </span>
                    {salePrice.toFixed(2)}
                </>
            ) : (
                <>${originalPrice.toFixed(2)}</>
            )}
        </div>
    )
}

export default Price
